.custom-checkbox {
  display: flex;
  align-items: center;

  span {
    width: 18px;
    height: 18px;
    flex: none;
    border: 1px solid #ddd;
    margin-right: 5px;
    background:#fff;

    .theme-dark &{
      background:#50555c;
    }

    &.mr-0 {
      margin-right: 0;
    }
  }

  input {
    display: none;
    &:checked + span {
      background: url('#{$image-path}/checkbox-img.png') no-repeat center !important;
      background-size: 100% 100%;
      border: none;
    }

    // radio  
    &[type='radio'] + span {
      border-radius: 50%;
    }
    &[type='radio']:checked + span {
      background: none !important;
      border: 5px solid #2e8bc5;
    }
  }

  p {
    font-size: 1rem;
    color:#000;
    @include theme('color', 'nav-block-name');
    white-space: nowrap;
    @media screen and (max-width: 768px) {
      white-space: normal;
    }

    em {
      font-style: normal;
    }
    .gray {
      color:#666666;
    }
  }

}
